import React, { useState, useEffect } from "react";
import {
  Container,
  TextField,
  Button,
  Snackbar,
  SnackbarContent,
  IconButton,
} from "@material-ui/core";
import api from "../api/api";
import { checkAuth } from "../actions";
import { connect } from "react-redux";
import { red } from "@material-ui/core/colors";
import { Close, Error } from "@material-ui/icons";

const Login = (props) => {
  const { checkauth } = props;
  const [formvalues, setformvalues] = useState({ email: "", password: "" });
  const [wait, setwait] = useState(false);
  const [toast, settoast] = useState(false);
  const [message, setmessage] = useState(null);

  useEffect(() => {
    document.title = "MOM: Login";
  }, []);

  const submit = async (e) => {
    e.preventDefault();
    try {
      setwait(true);
      const response = await api.post(`/api/admin/login`, formvalues);
      localStorage.setItem("mom-admin", response.data.token);
      setformvalues({ email: "", password: "" });
      setwait(false);
      return checkauth();
    } catch (err) {
      setwait(false);
      setmessage("Authentication failed. Try again!");
      return settoast(true);
    }
  };
  return (
    <Container maxWidth="xs">
      <div
        style={{
          marginTop: 200,
          border: "1px solid #ddd",
          borderRadius: 8,
          boxShadow: "1px 1px 1px #ddd",
          padding: 20,
        }}
      >
        <div
          style={{
            display: "flex",
            alignItems: "center",
            flexDirection: "column",
          }}
        >
          <img
            alt="Logo"
            style={{
              height: 90,
              width: 90,
              borderRadius: 50,
              cursor: "pointer",
            }}
            src="/logo512.png"
          />
        </div>
        <form onSubmit={submit}>
          <TextField
            required
            fullWidth
            variant="outlined"
            autoFocus
            type="email"
            margin="normal"
            size="small"
            label="Email"
            value={formvalues.email}
            onChange={(e) =>
              setformvalues({ ...formvalues, email: e.target.value })
            }
          />
          <TextField
            required
            type="password"
            fullWidth
            variant="outlined"
            margin="normal"
            size="small"
            label="Password"
            value={formvalues.password}
            onChange={(e) =>
              setformvalues({ ...formvalues, password: e.target.value })
            }
          />
          <div
            style={{
              display: "flex",
              justifyContent: "flex-end",
              marginTop: 10,
            }}
          >
            <Button
              disabled={wait}
              type="submit"
              variant="contained"
              color="primary"
            >
              Login
            </Button>
          </div>
        </form>
      </div>
      <Snackbar
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
        open={toast}
        autoHideDuration={3000}
        onClose={() => {
          settoast(false);
        }}
      >
        <SnackbarContent
          style={{ backgroundColor: red[700] }}
          message={
            <span style={{ display: "flex", alignItems: "center" }}>
              <Error style={{ marginRight: 5 }} />
              {message}
            </span>
          }
          onClose={() => {
            settoast(false);
          }}
          action={
            <IconButton
              key="close"
              color="inherit"
              onClick={() => settoast(false)}
            >
              <Close />
            </IconButton>
          }
        ></SnackbarContent>
      </Snackbar>
    </Container>
  );
};

export default connect(null, { checkauth: checkAuth })(Login);
